.animate-bottom-border::after {
  transition: width 250ms;
  @apply block h-[2px] w-0 bg-green content-[''];
}

.about-me-button {
  @apply mb-6 flex animate-appear-slow items-center justify-center gap-4 text-lg text-slate-l sm:mb-10 sm:text-xl;
}

.about-me-button:hover > .animate-bottom-border::after {
  @apply w-full;
}

.about-me-button:focus > .animate-bottom-border::after {
  @apply w-full;
}
